export const gpsAccess = (setinputType: React.Dispatch<React.SetStateAction<string>>, setgpsVal: React.Dispatch<React.SetStateAction<GeolocationPosition | undefined>>, setTimeStamp: React.Dispatch<React.SetStateAction<string | undefined>>) => {
    setinputType("GPS");
    navigator.geolocation.getCurrentPosition((pos) => {
      setgpsVal(pos);

      let unix_timestamp = pos.timestamp;
      let date = new Date(unix_timestamp * 1000);
      let hours = date.getHours();
      let minutes = "0" + date.getMinutes();
      let seconds = "0" + date.getSeconds();

      // Will display time in 10:30:23 format
      let formattedTime = hours + ":" + minutes + ":" + seconds;

      setTimeStamp(formattedTime);
    });
  };