import { useRef, useState } from "react";
import "./App.css";
import {
  bluetoothAccess,
  readBluetoothData,
  writeBluetoothData,
} from "./pwa/bluetooth";
import { gpsAccess } from "./pwa/gps";
import { storageAccess } from "./pwa/inputStorage";
import { micAccess } from "./pwa/microphone";

const getBackCamera = () => ({ exact: "environment" });
const getFrontCamera = () => "user";
const enum CameraType {
  front,
  back,
}

type CameraPropertyType = string | {exact:string}

function App() {
  const [inputType, setinputType] = useState<string>("Empty");
  const [canvas, setCanvas] = useState<HTMLCanvasElement | any>();
  const [gpsVal, setgpsVal] = useState<GeolocationPosition>();
  const [timestamp, setTimeStamp] = useState<string>();
  const [service, setService] = useState<any>();
  const [device, setDevice] = useState<any>();
  const [filename, setFilename] = useState<string>();
  const [message, setMessage] = useState<string>();
  const [inputText, setInputText] = useState<string>();
  const cameraRef = useRef<HTMLVideoElement>(null);
  const videoReference = useRef<HTMLVideoElement>(null);
  const [cameraStream, setCameraStream] = useState<MediaStream | null>(null);
  const [cameraType, setCameraType] = useState<CameraType>(CameraType.front);

  const cameraAccess = (ctype?: CameraPropertyType | null | undefined) => {
    setinputType("Camera");
    startCamera(1, ctype);
  };

  const inputRef = useRef<HTMLInputElement>(null)
  const [imageSrc, setImageSrc] = useState<string|null>(null)

  const videoPlay = () => {
    setinputType("Video");
    startCamera(2);
  };

  const setCameraFlip = (cam: number) => {
    console.log({cameraType})
    stopStream();
    if (cameraType === CameraType.back) {
      setCameraType(CameraType.front);
      if (cam === 1) {
        cameraAccess(getFrontCamera());
      } else if (cam === 2) {
        videoPlay();
      }
    } else {
      setCameraType(CameraType.back);
      if (cam === 1) {
        cameraAccess(getBackCamera());
      } else if (cam === 2) {
        videoPlay();
      }
    }

    
  };

  const gpsPermission = () => {
    gpsAccess(setinputType, setgpsVal, setTimeStamp);
  };

  const bluetoothPermission = () => {
    bluetoothAccess(setinputType, setDevice, setService);
  };

  const readBluetoothValue = async () => {
    readBluetoothData(service, device, setMessage);
  };

  const setWriteData = (val: any) => {
    setInputText(val.target.value);
  };

  const writeBluetoothValue = async () => {
    writeBluetoothData(service, inputText);
  };

  const micPermission = async () => {
    micAccess(setinputType);
  };

  const storagePermission = async (val: any) => {
    console.log({l:inputRef.current})
    if(inputRef.current?.files){
      const files = inputRef.current.files
      console.log({files})
      // if(imageRef.current) {
        // var oFReader = new FileReader();
        // oFReader.readAsDataURL(files[0])
        // oFReader.onload = function (oFREvent) {
        //   imageRef.current.src = oFREvent.target.result;
        // };
        const url= URL.createObjectURL(files[0]);
        setImageSrc(url);
        console.log({url})
      // }
      storageAccess(val, setinputType, setFilename);
    }
  };

  const startCamera = async (device: number, camerGDevice?: CameraPropertyType | null | undefined) => {
    const deviceSelected = device === 1 ? cameraRef : videoReference;
    console.log({cameraType})

    let cameraDevice:CameraPropertyType;

    if (camerGDevice){
      cameraDevice = camerGDevice;
    }
    else{
      cameraDevice = (cameraType === CameraType.front) ? getFrontCamera() : getBackCamera();
    }
   

    navigator.mediaDevices
      .getUserMedia({
        video: {
          width: { min: 1024, ideal: 1280, max: 1920 },
          height: { min: 576, ideal: 720, max: 1080 },
          // facingMode: { exact: "environment" },
          facingMode: cameraDevice,
        },
      })
      .then((stream) => {
        // If Video Element Exist - Assign to that Element
        if (deviceSelected.current) {
          deviceSelected.current.srcObject = stream;
        }

        // Assigning Camera Stream to Value
        setCameraStream(stream);
      })
      .catch((error) => {
        // setError(error.message);
      });

  };

  const takeImage = () => {
    let context = canvas.getContext("2d");

    if (cameraRef.current != null) {
      const video = cameraRef.current;
      canvas.height = video.videoHeight;
      canvas.width = video.videoWidth;
      context.drawImage(
        video,
        0,
        0,
        canvas.width,
        video.videoHeight / (video.videoWidth / canvas.width)
      );
    }
  };

  const stopStream = () => {
    // setStopAction(true);
    setinputType("Empty");
    if (cameraStream) {
      // if (!cameraStream.stop()){
      cameraStream.getTracks().forEach(function (track) {
        track.stop();
      });
      setCameraStream(null);
      // }
    }
  };

  return (
    <div className="App">
      <div className="img-block">
        {/* <img className="utt-logo" src="Utthunga.png" alt="Utthunga" /> */}
      </div>
      <h4>Click on permission name to give Access</h4>
      <div id="cc" className="btn-blocks">
        <button id="camera" onClick={() => cameraAccess()}>
          Camera
        </button>
        <button id="camera" onClick={videoPlay}>
          Video
        </button>
        <button onClick={gpsPermission}>GPS</button>
        <button onClick={bluetoothPermission}>Bluetooth</button>
        <button onClick={micPermission}>Microphone</button>
        {/* <button onClick={storageAccess}>Storage</button> */}
        <input type="file" name="Select File" 
        onChange={storagePermission} 
          ref={inputRef}
        />
      </div>

      <div className="output-blocks">
        <div className="output-header">
          <h4>Outputs</h4>
        </div>
        {inputType === "Camera" && (
          <div className="output-visible">
            <div className="input-block">
              <video
                ref={cameraRef}
                controls={false}
                autoPlay
                style={{ width: "100%", maxWidth: 300 }}
              ></video>
            </div>
            <div className="click-btn">
              <button onClick={() => setCameraFlip(1)}>Flip camera</button>
              <button onClick={takeImage}>Take Photo</button>
              <button onClick={stopStream}>Stop</button>
            </div>
            <div className="output-block">
            <canvas className="output-image" ref={(c) => setCanvas(c)} />
          </div>
          </div>
        )}
        {inputType === "Video" && (
          <div className="output-visible">
            <div className="input-block">
              <video
                ref={videoReference}
                controls={true}
                autoPlay
                style={{ width: "100%", maxWidth: 300 }}
              ></video>
            </div>
            <div className="click-btn">
              <button onClick={stopStream}>Stop</button>
            </div>
          </div>
        )}
        {inputType === "GPS" && (
          <div className="outputBlock">
            <div>Latitude : {gpsVal?.coords.latitude}</div>
            <div>Longitude : {gpsVal?.coords.longitude}</div>
            <div>Time : {timestamp}</div>
          </div>
        )}
        {inputType === "Bluetooth" && (
          <div className="btn-block">
            <button onClick={readBluetoothValue}>Read Data</button>
            <div>Message : {message}</div>
            <input type="text" onChange={setWriteData} />
            <button onClick={writeBluetoothValue}>Write Data</button>
          </div>
        )}
        {inputType === "Storage" && (
          <div className="btn-block">
            <div>Selected File : {filename}</div>
            {imageSrc ? <img src={imageSrc} alt="some" height={200} width={200}/> : ''}
          </div>
        )}
      </div>
    </div>
  );
}

export default App;
