export const storageAccess = async (val: any, setinputType: React.Dispatch<React.SetStateAction<string>>, setFilename: React.Dispatch<React.SetStateAction<string | undefined>>) => {
    setinputType("Storage");
    let nameVal = val.target.value.split("\\");
    console.log({val, l: val.target.value})
    setFilename(nameVal[(nameVal.length - 1)]);
  };