export const bluetoothAccess = (setinputType:React.Dispatch<React.SetStateAction<string>>, setDevice:React.Dispatch<any>, setService:React.Dispatch<any>) => {
    setinputType("Bluetooth");
    let options = {
      filters: [
        { services: ["0000fff1-0000-1000-8000-00805f9b34fb"] },
        { services: ["0000fff0-0000-1000-8000-00805f9b34fb"] },
        { services: ["0000fff4-0000-1000-8000-00805f9b34fb"] },
        { services: ["00002904-0000-1000-8000-00805f9b34fb"] },
        { name: "Nayan" },
      ],
    };
    // @ts-ignore
    navigator.bluetooth
      .requestDevice(options)
      .then(async (device: any) => {
        setDevice(device);
        return await device.gatt.connect();
      })
      .then((server: any) => {
        return server.getPrimaryService("0000fff0-0000-1000-8000-00805f9b34fb");
      })
      .then((service: any) => {
        setService(service);
      })
      
  };

  export const readBluetoothData = async(service:any, device:any, setMessage:React.Dispatch<React.SetStateAction<string | undefined>>) => {
    if(service != null){
      let message = await service.getCharacteristic(
        "0000fff1-0000-1000-8000-00805f9b34fb"
      );
      message.startNotifications().then( (value: any) => {
        message.addEventListener("characteristicvaluechanged", (event: any) => {
          let value = event.target.value;
          const outValue = new TextDecoder().decode(value)
          setMessage(outValue);
        })
      })
    }
     
  }

  export const writeBluetoothData = async(service: any, inputText:string | undefined) => {
    if(service != null){
      let message = await service.getCharacteristic(
        "0000fff1-0000-1000-8000-00805f9b34fb"
      );
      let sendingMsg = new TextEncoder().encode(inputText);
      message.writeValueWithoutResponse(sendingMsg);
    }
  }